import React, { useEffect, useState } from "react";
import { Grid, Heading, Clock, Accordion, AccordionPanel, Box, Text, TextInput } from "grommet";
import { fetchProducts, fetchStoreById, bid } from "../helpers";
import Loading from "../components/Loading";
import Chat from './Chatroom'
import EventObserver from './EventObserver'
import ReactHtmlParser from 'react-html-parser';
import { getLatestBidder, getStoreBidInformation } from '../library/parse'
import { format } from 'date-fns'
import LiveStreamCommerce from '../containers/LiveStreamCommerce'
import ProductListing from '../containers/ProductListing'

const Home = ({
  location,
  pathContext,
  pageContext,
  storeName,
  id,
  ...props
}) => {
  const [products, setProducts] = useState([]);
  const [store, setStore] = useState(null);
  const [messages, setMessages] = useState([]);
  const [event, setEvent] = useState(null)
  const [bidPrices, setBidPrices] = useState({})
  const [bidStatus, setBidStatus] = useState({})
  const storeInformation  = store && ReactHtmlParser(`<div>${store.get("description")}`)

  const getProductLatestBid = async (id) => {
    const bid = await getLatestBidder(id)
    if(bid) {
      const newBidStatus = {
        ...bidStatus,
        [bid.get('object')]: {
          user: bid.get('user'),
          bid: bid.get('customField1'),
        }
      }
      setTimeout(() => setBidStatus(newBidStatus), 5000)

    }
  }
  useEffect(() => {

    fetchStoreById(id).then((e) => {
      setStore(e);
    });
    fetchProducts(id).then((e) => setProducts(e));

  }, [id]);

  useEffect(() => {
    const getLatestBid = async () => {
      const promises = await Promise.all(products.map(e => {
        return getLatestBidder(e.id)
      }, {}));
      setBidStatus(promises.filter(e => e).reduce((prev, next) => {
        prev[next.get('object')] = {
          user: next.get('user'),
          bid: next.get('customField1'),
        }
        return prev
      }, {}))
    }
    getLatestBid()
  }, [products]);

  useEffect(() => {
    if(event) {
      getProductLatestBid(event.get('object'));
      const message = event.get('customField2');
      const createdAt = format(event.get('createdAt'), 'hh:mm');
      event && setMessages(messages.concat([{ message: `${createdAt} - ${message}` }]))
    }    
  }, [event]);
  return (
    <>
      {
        !products.length && <Loading />
      }
              <Grid
          fill
          areas={[
            { name: 'nav', start: [0, 0], end: [0, 0] },
            { name: 'main', start: [1, 0], end: [1, 0] },
          ]}
          columns={['auto', 'flex']}
          rows={['flex']}
        >
          <Box gridArea="nav" direction="row" justify="center" align="center">
            
            <Box fill={true} align="center">
            <Heading level={3}> { store && store.get('name') } </Heading>
            {
              storeInformation
            }
            </Box>
          </Box>
          <Box gridArea="main" direction="row" align="center" justify="center">
          <LiveStreamCommerce products={products} />
          </Box>
        </Grid>
        <ProductListing products={products} />
    </>
  );
};

export default Home;
